import React from 'react'

import { Typography } from '@mui/material'

import Article from 'components/Layout/Article'

const AboutConsilium = () => {
  return (
    <Article title='О консилиуме'>
      <Typography variant='p' sx={styles.content}>
        Наше дело не так однозначно, как может показаться: убеждённость некоторых оппонентов не даёт нам иного выбора, кроме определения существующих финансовых и административных условий.
        <br /><br />
        В рамках спецификации современных стандартов, интерактивные прототипы неоднозначны и будут превращены в посмешище, хотя само их существование приносит несомненную пользу обществу. В своём стремлении повысить качество жизни, они забывают, что сплочённость команды профессионалов создаёт необходимость включения в производственный план целого ряда внеочередных мероприятий с учётом комплекса как самодостаточных, так и внешне зависимых концептуальных решений.
        <br /><br />
        В своём стремлении повысить качество жизни, они забывают, что курс на социально-ориентированный национальный проект играет определяющее значение для системы обучения кадров, соответствующей насущным.
      </Typography>
    </Article>
  )
}

export default AboutConsilium

const styles = {
  content: {
    fontSize: '1.2rem',
    fontWeight: '600',
    lineHeight: '30px',
    textAlign: 'justify'
  }
}