import React, { useMemo } from 'react'

import { Stack } from '@mui/material'

import Post from 'components/Post'

import { posts as postsBG } from 'utils/post'

const PostsList = () => {
    const posts = useMemo(() => [
        { title: 'О консилиуме', url: '/about-consilium' },
        { title: 'Инструкция пользователя', url: '/user-instruction' },
        { title: 'Моя история' },
        { title: 'Письменная расшифровка', url: '/written-decryption' },
        { title: 'Онлайн-Консультация с врачом из консилиума', url: '/online-consultation' },
        { title: 'Написать в тех. поддержку', url: '/technical-support' },
    ], [])

    return (
        <Stack direction='row' justifyContent='space-around'>
            {posts.map((post, id) => (
                <Post
                    key={id}
                    title={post.title}
                    icon={postsBG[id]}
                    url={post.url}
                />))}
        </Stack>
    )
}

export default PostsList